<template>
  <PageBody>
    <div class="list-special">
      <div class="list-header">
        <div class='list-header-left'>
          <div><span class='online-icon'></span>在线</div>
          <div><span class='outline-icon'></span>离线</div>
        </div>
      </div>
      <div class="terminal-table-container">
        <el-table
          v-loading="loading"
          :data="listData.data"
          border
          height="100%">
          <el-table-column
            prop="index"
            label="序号"
            align='center'
            width="64">
          </el-table-column>
          <el-table-column
            prop="address"
            align='center'
            label="详细地址">
          </el-table-column>
          <el-table-column
            prop="clubName"
            align='center'
            label="门店">
          </el-table-column>
          <el-table-column
            prop="version"
            align='center'
            label="终端机ID"
            width="128">
          </el-table-column>
          <el-table-column
            prop="machineStatus"
            align='center'
            label="状态"
            width="93">
            <template slot-scope='scope'>
              <span class="status-radius online" :class="{outline: !scope.row.status}"></span>
            </template>
          </el-table-column>
          <el-table-column
            prop="freeStorage"
            align='center'
            label="内存余量（G）"
            width="250">
            <template slot-scope='scope'>
              <div style="display: flex;justify-content: space-between;padding: 0 40px;">
                <span v-if="scope.row.memoryCap" :class="{'red-status': Number(scope.row.memoryCap.split(',')[0]) < 0.48}">上屏：{{scope.row.memoryCap.split(',')[0]}}</span>
                <span v-if="scope.row.memoryCap" :class="{'red-status': Number(scope.row.memoryCap.split(',')[1]) < 0.48}">中下屏：{{scope.row.memoryCap.split(',')[1]}}</span>
              </div>
            </template>
          </el-table-column>
          <el-table-column
            prop="productionTime"
            align='center'
            label="出厂时间"
            width="160">
          </el-table-column>
        </el-table>
      </div>
    </div>
  </PageBody>
</template>

<script>
import { getMachineInfo } from 'api/terminal'

export default {
  data () {
    return {
      listData: {},
      loading: true,
      memory: {}
    }
  },
  mounted () {
    this.getListData(this.$route.query)
  },
  methods: {
    getListData (data = {}) {
      this.loading = true
      let query = {
        pageSize: 10,
        pageNum: 1
      }
      this.query = Object.assign({}, query, data)
      getMachineInfo(this.query).then(res => {
        if (Number(res.code) === 200) {
          this.listData = this.countIndex(res)
        } else {
          this.listData = []
          this.$message({
            message: res.message,
            type: 'error'
          })
        }
      }).finally(() => {
        this.loading = false
      })
    },
    countIndex (res) {
      let {pageSize, pageNum, data} = res
      data.forEach((v, i) => {
        v.index = (pageSize * (Number(pageNum) - 1)) + i + 1
      })
      return res
    }
  }
}
</script>
<style lang="stylus" scoped>
  .list-special
    width 100%
    height 100%
    display flex
    flex-direction column
    align-items stretch
  .terminal-table-container
    flex 1
    overflow hidden
  .terminal-table-container /deep/ .el-table--enable-row-hover .el-table__body tr:hover > td
    background-color #fff
  .red-status
    color #F1524A
  .list-header-select-container
    margin-right 20px
  .list-header
    display flex
    justify-content space-between
    align-items center
    .list-header-left
      display flex
      height 64px
      align-items center
      padding-left 20px
    .list-header-right
      display flex
      align-items center
      font-size 12px
      color #999999
    .online-icon
      margin-right 5px
      display inline-block
      height 10px
      width 10px
      border-radius 50%
      background-color #66CC66
    .outline-icon
      margin-right 5px
      margin-left 60px
      display inline-block
      height 10px
      width 10px
      border-radius 50%
      background-color #FB0800
  .list-title
    font-size 18px
  .add-container
    display flex
    align-items center
  .add-icon
    display inline-block
    height 38px
    width 38px
    background url('~@/assets/set_up.png') no-repeat center
    background-size contain
    cursor pointer
  .add-text
    margin-left 10px
    font-size 14px
    color #666666
    cursor pointer
  #list-table
    margin-top 0
  .select-auth-td
    color #a8a8a8
  .el-checkbox-container
    display inline-block
    width 130px
    text-align center
  .select-auth-td-content
    overflow hidden
    white-space nowrap
    text-overflow ellipsis
    text-align center
    color #666666
  .terminal-item
    padding 0 20px
  .store-name
    margin 0 20px 0 10px
    display inline-block
    width 60px
  .more-store
    margin-top 20px
    color #217EE6
    cursor pointer
  .operation-text-button
    cursor pointer
    &:hover
      color #217EE6
  .operation-text-button:first-child
    margin-right 30px
  .status-radius
    display inline-block
    height 14px
    width 14px
    border-radius 50%
    &.online
      background-color #66CC66
    &.outline
      background-color #FB0800
  online outline
    // 编辑权限
  .edit-auth
    position absolute
    top 0
    width 100%
    background-color #ffffff
    z-index 1
</style>



