import request from 'utils/request'

export function getMemory (data) {
  return request({
    method: 'post',
    url: '/gym/machinemanagement/getMemorys',
    data
  })
}

/**
 * 用户模块
 */
// 终端机管理
export function getMachineInfo (data) {
  return request({
    method: 'post',
    url: '/gym/machinemanagement/listMachineManagement',
    data
  })
}



